<template>
    <div id="divMainFormData" v-cloak>
        <section>
            <tg-list v-show="IsServiceView" :IsShowAction="true" ModuleName="Ticketing" SubModuleCode="Ticketing"
                IdentityColumn="service_type_id" :HeaderText="$t('Services')" :listType="['List']" :LegendArray="[]"
                :ListData="ServiceListing" :RenderRowActionMethod="renderActions" :HeaderData="Headers"
                v-on:ActionButtonClick="actionButtonClick" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords"
                :SearchFields="leftSearchFields" :ListDataCallBackFunction="FetchRecords" :showCheckBox="true"
                :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
                v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
                v-on:LeftsearchButtonClick="leftCommonSearch" v-on:SortdataButtonClick="sortdata">
                <template v-slot:slotdata="{ data }">
                    <template v-if="data.column.COLUMN_NAME == 'status_id'">
                        <listing-status :isfor="'service'" v-if="isCRMShowStausDrop(data.row.status_id, 'Ticketing')"
                            ref="updatestatus" v-bind:dataid="data.row.service_type_id" data-controller="Crm"
                            v-bind:statuslist="StatusListForListing" v-bind:item="data.status_id" data-action="index"
                            v-bind:dataselected="data.row.status_id == 1001 ? 'Active' : 'Inactive'"
                            v-bind:callbackfunction="FetchRecords" v-bind:isselectedvaluestring="true">
                        </listing-status>
                        <a class="inactive-disabled" v-if="!isCRMShowStausDrop(data.row.status_id, 'Ticketing')">
                            <span v-bind:class="getStatusClass(data.row.status_id, 'status_id')">
                                {{ data.row[data.column.COLUMN_NAME] }}
                            </span>
                        </a>
                    </template>
                    <template v-if="data.column.COLUMN_NAME == 'cost'">
                        <span>
                            <span>$</span>{{ data.row.cost }}
                        </span>
                    </template>
                    <template v-if="data.column.COLUMN_NAME == 'unit_price'">
                        <span>
                            <span>$</span>{{ data.row.unit_price }}
                        </span>
                    </template>
                    <template v-if="data.column.COLUMN_NAME == 'created_at'">
                        <span>
                            {{ $options.filters.formatDateTime(data.row.created_at) }}
                        </span>
                    </template>
                    <template v-if="data.column.COLUMN_NAME == 'service_duration'">
                        <span>
                            {{ convertValue(data.row.service_duration) }}
                        </span>
                    </template>
                    <template v-if="data.column.COLUMN_NAME == 'service_name'">
                        <a href="javascript:;" data-toggle-tooltip="tooltip"
                            @click="ServiceTypeDetail(data.row.service_type_id)"
                            data-original-title="Click here to view Service">
                            {{ data.row[data.column.COLUMN_NAME] }}
                        </a>
                    </template>
                </template>
            </tg-list>
        </section>
        <ServiceDetail v-if="ServiceDetailPopup" :ServiceDetailRecord="ServiceDetailRecord">
        </ServiceDetail>


    </div>
</template>
<script>
import Schema from "@/views/Service/JS/ServiceListingSchema";
import DataServices from "@/services/DataService";
import ServiceDetail from '../../components/Modal/modalServiceDetail.vue';
export default {
    components: {
        ServiceDetail
    },

    data() {
        return {
            noRecords: false,
            TotalRecords: 0,
            ServiceDetailPopup: false,
            CurrentPage: 1,
            TotalPages: 0,
            listheaderbuttons: Schema.buttons,
            Headers: Schema.Headers,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            StatusListForListing: [{ STATUS: 'Active', STATUS_ID: 1001, STATUS_COLOR: "#ffffff", STATUS_BG_COLOR: "#28a745" },
            { STATUS: 'Inactive', STATUS_ID: 1002, STATUS_COLOR: "#ffffff", STATUS_BG_COLOR: "#d72435" }],
            SortBy: '',
            SortExp: '',
            PageSize: "10",
            PageNumber: "1",
            GroupStart: 1,
            GroupEnd: 3,
            SearchCondition: '',
            ServiceListing: [],
            ServiceDetailRecord: [],
            conditionForInstantSearch: "",
            IsServiceView: true
        }
    },
    created: async function () {
        var vm = this;
        await vm.FetchRecords();
        await Schema.bindleftCommonSearchdropdown(vm, "Ticketing", "USERS");
        await Schema.bindleftCommonSearchdropdown(vm, "Ticketing", "Work_Type_List");
    },
    methods: {
        convertValue: function (durationValue) {
            var value = parseInt(durationValue);
            var hours = Math.floor(value / 60);
            var minutes = value % 60;

            if (hours === 0) {
                return `${minutes} minute${minutes !== 1 ? 's' : ''}`;
            } else if (minutes === 0) {
                return `${hours} hour${hours !== 1 ? 's' : ''}`;
            } else {
                return `${hours} hour${hours !== 1 ? 's' : ''} ${minutes} minute${minutes !== 1 ? 's' : ''}`;
            }
        },

        FetchRecords: async function () {
            //debugger;
            var vueObj = this;
            var srchCondition = "";
            var url = `sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&pagesize=${vueObj.PageSize}&pagenumber=${vueObj.PageNumber}`;
            if (!this.isBlank(this.searchCondition)) {

                srchCondition = srchCondition + this.searchCondition;
            }
            if (!this.isBlank(this.conditionForInstantSearch)) {
                srchCondition = srchCondition + ((!this.isBlank(this.searchCondition)) ? (" AND " + this.conditionForInstantSearch) : this.conditionForInstantSearch);
            }
            if (srchCondition != "") {
                url = url + "&search=" + srchCondition;
            }
            await DataServices.GetServiceListing(url).then(function (response) {
                //debugger;
                if (response.data != "" && response.data != null) {
                    vueObj.Headers.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == "status_id") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                        if (column.COLUMN_NAME == "service_name") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                        if (column.COLUMN_NAME == 'created_at') {
                            column.settings = {
                                isInSlot: true
                            }
                        }
                        if (column.COLUMN_NAME == 'cost') {
                            column.settings = {
                                isInSlot: true
                            }
                        }
                        if (column.COLUMN_NAME == 'unit_price') {
                            column.settings = {
                                isInSlot: true
                            }
                        }
                        if (column.COLUMN_NAME == 'service_duration') {
                            column.settings = {
                                isInSlot: true
                            }
                        }
                    });
                    if (response.data.result.data.length > 0) {
                        vueObj.ServiceListing = response.data.result.data;
                        vueObj.TotalRecords = response.data.result.data[0].TOTALRECORDS;
                        vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                        vueObj.CurrentPage = vueObj.PageNumber;
                        vueObj.noRecord = false;
                    } else {
                        vueObj.noRecord = true;
                        vueObj.TotalRecords = 0
                        vueObj.ServiceListing = [];
                    }
                }
                else {
                    vueObj.noRecord = true;
                    vueObj.TotalRecords = 0
                    vueObj.ServiceListing = [];
                }
                setTimeout(function () { vueObj.ResponsiveDataTable('tablelistingdata') }, 500);
            })
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchRecords();
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchRecords();
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.FetchRecords();
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'View':
                        action.isVisible = true;
                        break;
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    case 'Publish':
                        action.isVisible = row.is_publish == false ? true : false;
                        break;
                    case 'Unpublished':
                        action.isVisible = row.is_publish == true ? true : false;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        actionButtonClick(callbackfunction, item, index, event) {
            switch (callbackfunction) {
                case 'ClickOnView':
                    this.ServiceTypeDetail(item.service_type_id);
                    break;
                case 'Edit':
                    this.EditServiceDetail(item)
                    break;
                case 'Publish':
                    this.PublishDetail(item);
                    break;
                case 'Unpublished':
                    this.UnpublishedDetail(item);
                    break;
            }
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "commonDeleteData":
                    this.DeleteServiceData();
                    break;
                case "openAddService":
                    this.OpenManagePopup(null);
                    break;
                case "updateStatus":
                    this.UpdateStatus(event.id);
                    break;
                case "DownloadExcelDataNew":
                    this.DownloadExcelDataNew();
                    break;
                case "ImportService":
                    this.ImportService();
                    break;
            }
        },
        OpenManagePopup: function () {
            var vm = this;
            vm.$router.push({ name: 'AddService', params: { id: 0 } });
        },
        ImportService: function () {
            var vm = this;
            vm.$router.push({ name: 'ImportService' });
        },
        EditServiceDetail: function (data) {
            var vm = this;
            vm.$router.push({ name: 'EditService', params: { id: data.service_type_id } });
        },
        PublishDetail: function (data) {
            var vm = this;
            vm.$router.push({ name: 'PublishService', params: { id: data.service_type_id } });
        },
        UnpublishedDetail: function (data) {
            var vm = this;
            //debugger;
            vm.confirmR(vm.$t('UnpublishConfirmMsg'), true, vm.$t('Confirm'), function (result) {
                var url = `serviceTypeId=${data.service_type_id}`;
                DataServices.UpdatePublishDetail(url)
                    .then(function (response) {
                        //debugger;
                        if (response.data.status == 'Success') {
                            if (response.data.result == 1) {
                                vm.ShowAlert(vm.$t("UnpublishSuccessMsg"), "success", vm.$t('Alert'));
                                vm.FetchRecords();
                            }
                            else {
                                vm.ShowAlert(vm.$t("ErrorWhileSaving", [vm.$t("Unpublished")]), "danger", true, vm.$t("Alert"));
                            }
                        }
                    });
            });
        },
        DeleteServiceData: function (event) {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Service")), function () {
                    var postJSON = {
                        ids: val,
                        companyId: vm.companyId,
                        userId: vm.user_id,
                        moduleName: "TICKETING",
                        refCode: "TALYGEN_TICKET_SERVICE"
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataServices.deleteActivityData(objectPostString).then(response => {
                        var box = "";
                        if (response.data[0].Status == "Success") {
                            $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                            $("[id^='DeleteMultiple']").removeClass('enable').addClass('disabled');
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            vm.FetchRecords();
                            box = vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Service')]), "Success", true, vm.$t('Alert'));
                        }
                        else {
                            box = vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Service')]), "failure", true, vm.$t('Alert'));
                        }
                        setTimeout(function () {
                            box.modal('hide');
                        }, 2000);

                    });
                });
            }
            else {
                alert(vm.$t("Selectanyrecordtodelete"));
            }
        },
        DownloadExcelDataNew: function () {
            var vueObj = this;
            if (!vueObj.isBlank(vueObj.SearchCondition)) {
                vueObj.PageNumber = 1;
            }

            var url = `sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&pagesize=${vueObj.PageSize}&pagenumber=${vueObj.PageNumber}`;
            if (!vueObj.isBlank(vueObj.SearchCondition)) {
                url = url + "&search=" + vueObj.SearchCondition
            }
            DataServices.GetServiceListing(url)
                .then(function (response) {
                    if (response.data.status == "Success") {
                        if (response.data.result == null) {
                            vueObj.ShowAlertShowAlert($t("NoRecordfound"), "warning", true, $t("Alert"));
                        } else {
                            //debugger;
                            var obj = {
                                'Service Name': '',
                                'WorkType Name': '',
                                'Cost($)': '',
                                'Mark up(%)': '',
                                'UnitPrice($)': '',
                                'Service Duration': '',
                                'Status': '',
                                'Created By': '',
                                'Created At': ''
                            }
                            var dataArray = [];

                            response.data.result.data.forEach(function (item) {
                                var newObj = Object.assign({}, obj);
                                newObj['Service Name'] = item.service_name;
                                newObj['WorkType Name'] = item.worktype_name;
                                newObj['Cost($)'] = item.cost;
                                newObj['Mark up(%)'] = item.markup;
                                newObj['UnitPrice($)'] = item.unit_price;
                                newObj['Service Duration'] = item.service_duration;
                                newObj['Status'] = item.status_id == '1001' ? 'Active' : 'Inactive';
                                newObj['Created By'] = item.created_by;
                                newObj['Created At'] = vueObj.$options.filters.formatDateTime(item.created_at);
                                dataArray.push(newObj);
                            });
                            vueObj.JSONToCSVConvertorData(dataArray, "Service-Detail", true);
                        }
                    }

                });
        },
        ServiceTypeDetail: async function (ths) {
            var vm = this;
            vm.ServiceDetailRecord = [];
            var url = `serviceTypeId=${ths}`;
            await DataServices.GetServiceDetail(url).then(
                function (response) {
                    debugger
                    vm.ServiceDetailRecord = response.data.result.data;
                    vm.ServiceDetailPopup = true;
                    console.log(vm.ServiceDetailRecord);
                });
        },
        UpdateStatus: async function (status) {
            debugger
            var vm = this;
            if (status == 'Active') {
                status = 1001
            }
            else {
                status = 1002
            }
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmActiveInactiveStatus"), true, (vm.$t("Active") + "  " + vm.$t("Inactive")), function () {
                    var url = `id=${val}&statusId=${status}&isFor=${'service'}`;
                    DataServices.updatestatus(url).then(response => {
                        if (response.data == true) {
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("Status")]), "success", true, vm.$t("Alert"));
                            vm.FetchData();
                        }
                    })
                })
            }
        }
    },
}
</script>